import { filterActions, loadActions } from './lib/bundle-utility';

const loaded = loadActions(filterActions([
  {
    selector: '.site-main',
    load: [require('./modules/sign-in-out')],
  },
  {
    selector: '.signed-in-list',
    load: [require('./modules/results')],
  },
]));



