import { $win, $body } from 'constants/elements';

const $dialog = $('dialog');
const enter = 13;
const esq = 27;
const items = JSON.parse(localStorage.getItem('items')) || [];
let message = '';

const date = new Date();
const day = date.getDate();
const month = date.getMonth() + 1;
const year = date.getYear() - 100 + 2000;

let idList = populateList();
let studentList = [...getStudents()];

function populateList() {
  let idList = [];
  if (window.localStorage.length) {
    const oldList = localStorage.getItem("items");
    const oldIdList = JSON.parse(oldList); //var is now re-loaded!
    let idList = [...oldIdList];
    return idList;
  }
  return idList;
}

function getStudents() {
  const xmlhttp = new XMLHttpRequest();
  let object = [];

  xmlhttp.onreadystatechange = function () {
    if (this.readyState === 4 && this.status === 200) {
      object = this.responseText;
    }
  };

  xmlhttp.open('GET', '/_src/js/data/students.json', false);
  xmlhttp.send();
  const students = JSON.parse(object);
  let studentList = [...students];

  return studentList;
}

function signInOut() {
  message = `Welcome to the Library!`;
  $dialog.html(message);
  const time = new Date();
  const hour = time.getHours();
  const minutes = time.getMinutes();
  const seconds = time.getSeconds();

  const $studentID = $('.sign-in-input').val();
  const $signInIDS = $('.tr .id-number');

  if ($studentID.length) {
    const $student = [`${month}-${day}-${year}`, $studentID,`${hour > 12 ? hour -12 : hour}:${minutes < 10 ? '0' : ''}${minutes}:${seconds < 10 ? '0' : ''}${seconds}`, '-'];

    for (let $i = 0; $i < studentList.length; $i++) {
      if (studentList[$i].id == $studentID) {
        message = `Welcome to the Library, ${studentList[$i].fname}!`;
        $student.push(`${studentList[$i].fname}`, `${studentList[$i].lname}`);
        $dialog.html(message);
      }
    }

    $dialog.addClass('active');
    setTimeout(function(){
      $dialog.removeClass('active');
    }, 2500);

    for (let $i = 0; $i < idList.length; $i++) {
      if ($studentID === idList[$i][1]) {
        message = `Come Back Soon!`;

        if (idList[$i][3] === '-') {
          $dialog.html(message);
          $dialog.addClass('active');
          setTimeout(function(){
            $dialog.removeClass('active');
            message = `Welcome to the Library`;
          }, 2500);
          idList[$i][3] = `${hour > 12 ? hour -12 : hour}:${minutes < 10 ? '0' : ''}${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
          localStorage.setItem('items', JSON.stringify(idList));
          return;
        } else {
          idList.unshift(...[$student]);
          localStorage.setItem('items', JSON.stringify(idList));
          return;
        }
      }
    }
    idList.unshift(...[$student]);
    localStorage.setItem('items', JSON.stringify(idList));

    $('.sign-in-input').val('');
  } else {
    alert('Please enter your student ID');
  }
}

function keySignInOut(event) {
  if (event.keyCode === enter) {
    signInOut();
    $('.sign-in-input').val('');
  }

  if (event.keyCode === esq) {
    $('.sign-in-input').val('');
  }
}

export default function signInDefault() {
  $body.on('click', '.sign-in-button', signInOut);
  $win.on('keyup', keySignInOut);
}



