import { $win, $body } from 'constants/elements';

let idList = populateList();

const date = new Date();
const day = date.getDate();
const month = date.getMonth() + 1;
const year = date.getYear() - 100 + 2000;

function populateList() {
  let idList = [];
  if (window.localStorage.length) {
    const oldList = localStorage.getItem("items");
    const oldIdList = JSON.parse(oldList); //var is now re-loaded!
    let idList = [...oldIdList];
    output(idList);
    return idList;
  }
  return idList;
}

function output(students) {
  $('.lists').empty();
  students.forEach((student) => {
    $('.lists').append(
      `<div class="tr">
        <div class="date-list">
          <span class="td date">${student[0]}</span>
        </div>
        <div class="time-in-list">
          <span class="td time-in">${student[2]}</span>
        </div>
        <div class="time-out-list">
          <span class="td time-out">${student[3] === undefined ? ' - ' : student[3]}</span>
        </div>
        <div class="student-name">
          <span class="td name">${student[4] === undefined ? ' - ' : student[4]} ${student[5] === undefined ? '' : student[5]} </span>
        </div>
        <div class="id-list">
          <span class="td id-number">${student[1]}</span>
        </div>
      </div>`
    );
  });
}

function writeToFile(e) {
  e.preventDefault();

  let csvContent = "data:text/csv;charset=utf-8,";
  idList.forEach(function(rowArray){
    const row = rowArray.join(",");
    csvContent += row + "\r\n";
  });

  const encodedUri = encodeURI(csvContent);
  const link = document.createElement('a');

  link.setAttribute("href", encodedUri);
  link.setAttribute("download",  `${month}-${day}-${year}.csv`);
  document.body.appendChild(link); // Required for FF

  link.click(); // This will download the data file named "my_data.csv".
}

function clearData(e) {
  e.preventDefault();
  idList = [];
  $('.lists').empty();
  localStorage.clear();
}

export default function resultsDefault() {
  $body.on('click', '.clear-button', clearData);
  $body.on('click', '.generate-button', writeToFile);
}



